












































































































































































import { Vue, Component, Watch } from "vue-property-decorator";
import CopyText from "@/components/misc/CopyText.vue";
import QRModal from "@/components/modals/QRModal.vue";
import AvaAsset from "@/js/AvaAsset";
import { ONEDJTX } from "avalanche/dist/utils";
import Modal from "@/components/modals/Modal.vue";
import GoldChartthree from "@/components/PlanetChartthree.vue";
import { bnToBig } from "@/helpers/helper";
import { ChainIdType } from "@/constants";
import { priceDict } from "@/store/types";
import { WalletType } from "@/js/wallets/types";
import PaperWallet from "@/components/modals/PaperWallet/PaperWallet.vue";
import QRCode from "qrcode";
import Big from "big.js";
import { BN } from "avalanche/dist";

import {
  UTXOSet as AVMUTXOSet,
  UTXO as AVMUTXO,
  AVMConstants,
} from "avalanche/dist/apis/avm";
import {
  UTXOSet as PlatformUTXOSet,
  UTXO as PlatformUTXO,
  PlatformVMConstants,
  StakeableLockOut,
} from "avalanche/dist/apis/platformvm";

@Component({
  components: { Modal, CopyText, QRModal, PaperWallet, GoldChartthree },
  filters: {
    cleanDjtxBN(val: BN) {
      let big = Big(val.toString()).div(Big(ONEDJTX.toString()));
      return big.toLocaleString(2);
    },
  },
  methods: {
    currentDate() {
      const current = new Date();
      const date = `${current.getDate()}/${
        current.getMonth() + 1
      }/${current.getFullYear()}`;
      return date;
    },
  },
})
export default class MethodChainModal extends Vue {
  colorLight: string = "#FFF";
  colorDark: string = "#000000";
  chainNow: ChainIdType = "M";
  chain = "M";

  $refs!: {
    modal: Modal;
    qr_modal: QRModal;
    print_modal: PaperWallet;
    qr: HTMLCanvasElement;
  };
  @Watch("address")
  onaddrchange() {
    this.updateQR();
  }
  open(): void {
    let modal = this.$refs.modal as Modal;
    //@ts-ignore
    modal.open();
  }
  isActive: boolean = false;

  public close() {
    this.$emit("beforeClose");
    this.isActive = false;
  }

  goToTransfer() {
    this.$refs.modal.close(); // or whatever your modal close method is
    this.$router.push("/wallet/transfer");
  }

  goToCrossChain() {
    this.$refs.modal.close(); // or whatever your modal close method is
    this.$router.push("/wallet/cross_chain");
  }

  updateBalance(): void {
    this.$store.dispatch("Assets/updateUTXOs");
    this.$store.dispatch("History/updateTransactionHistory");
  }

  viewQRModal() {
    // @ts-ignore
    this.$refs.qr_modal.open();
  }
  viewPrintModal() {
    let modal = this.$refs.print_modal;
    // @ts-ignore
    modal.open();
  }
  updateQR() {
    let canvas = this.$refs.qr as HTMLCanvasElement;
    if (!canvas) return;

    let size = canvas.clientWidth;
    QRCode.toCanvas(
      canvas,
      this.address,
      {
        scale: 6,
        color: {
          light: this.colorLight,
          dark: this.colorDark,
        },
        width: size,
        // height: size,
      },
      function (error: any) {
        if (error) console.error(error);
      }
    );
  }

  setChain(chainID: string) {
    this.chain = chainID;
  }

  get ava_asset(): AvaAsset | null {
    let ava = this.$store.getters["Assets/AssetAVA"];
    return ava;
  }

  get wallet(): WalletType | null {
    return this.$store.state.activeWallet;
  }

  get avmUnlocked(): BN {
    if (!this.ava_asset) return new BN(0);
    return this.ava_asset.amount;
  }

  get djtxPriceText() {
    return this.priceDict.usd;
  }

  get totalBalanceUSDText(): string {
    if (this.isUpdateBalance) return "--";
    return this.totalBalanceUSD.toLocaleString(2);
  }

  // should be unlocked (X+P), locked (X+P) and staked and lockedStakeable
  get balanceText(): string {
    if (this.ava_asset !== null) {
      let denom = this.ava_asset.denomination;
      return this.totalBalanceBig.toLocaleString(denom);
    } else {
      return "?";
    }
  }

  get balanceTextLeft(): string {
    if (this.isUpdateBalance) return "--";
    let text = this.balanceText;
    if (text.includes(".")) {
      let left = text.split(".")[0];
      return left;
    }
    return text;
  }

  get balanceTextRight(): string {
    if (this.isUpdateBalance) return "";
    let text = this.balanceText;
    if (text.includes(".")) {
      let right = text.split(".")[1];
      return right;
    }
    return "";
  }

  // Locked balance is the sum of locked DJTX tokens on X and P chain
  get balanceTextLocked(): string {
    if (this.isUpdateBalance) return "--";

    if (this.ava_asset !== null) {
      let denom = this.ava_asset.denomination;

      // let otherLockedAmt = this.platformLocked.add(this.platformLockedStakeable)
      let amt = this.ava_asset.getAmount(true);

      return amt.toLocaleString(denom);
    } else {
      return "--";
    }
  }

  get balanceTextMultisig() {
    if (this.isUpdateBalance) return "--";

    if (this.ava_asset !== null) {
      let denom = this.ava_asset.denomination;
      return bnToBig(this.avmMultisig, denom).toLocaleString();
    } else {
      return "--";
    }
  }

  get avmMultisig(): BN {
    if (this.ava_asset !== null) {
      return this.ava_asset.amountMultisig;
    } else {
      return new BN(0);
    }
  }

  get unlockedText() {
    if (this.isUpdateBalance) return "--";

    if (this.ava_asset) {
      let xUnlocked = this.ava_asset.amount;

      let denom = this.ava_asset.denomination;

      let tot = xUnlocked;

      let amtBig = bnToBig(tot, denom);

      return amtBig.toLocaleString(denom);
    } else {
      return "--";
    }
  }

  get avmLocked(): BN {
    if (!this.ava_asset) return new BN(0);
    return this.ava_asset.amountLocked;
  }

  get evmUnlocked(): BN {
    if (!this.wallet) return new BN(0);
    // convert from ^18 to ^9
    let bal = this.wallet.ethBalance;
    return bal.div(new BN(Math.pow(10, 9).toString()));
  }

  get totalBalance(): BN {
    if (!this.ava_asset) return new BN(0);

    let tot = this.ava_asset.getTotalAmount();
    // add EVM balance
    tot = tot.add(this.evmUnlocked);
    return tot;
  }

  get totalBalanceBig(): Big {
    if (this.ava_asset) {
      let denom = this.ava_asset.denomination;
      let bigTot = bnToBig(this.totalBalance, denom);
      return bigTot;
    }
    return Big(0);
  }

  get evmBalanceBig(): Big {
    if (this.ava_asset) {
      let denom = this.ava_asset.denomination;
      let bigTot = bnToBig(this.evmUnlocked, denom);
      return bigTot;
    }
    return Big(0);
  }

  get methodBalanceBig(): Big {
    if (this.ava_asset) {
      let denom = this.ava_asset.denomination;
      let bigTot = bnToBig(this.platformUnlocked, denom);
      return bigTot;
    }
    return Big(0);
  }

  get totalBalanceUSD(): Big {
    let usdPrice = this.priceDict.usd;
    let usdBig = this.totalBalanceBig.times(Big(usdPrice));
    return usdBig;
  }

  get evmBalanceUSD(): Big {
    let usdPrice = this.priceDict.usd;
    let usdBig = this.evmBalanceBig.times(Big(usdPrice));
    return usdBig;
  }

  get methodBalanceUSD(): Big {
    let usdPrice = this.priceDict.usd;
    let usdBig = this.methodBalanceBig.times(Big(usdPrice));
    return usdBig;
  }

  get evmBalanceUSDText(): string {
    if (this.isUpdateBalance) return "--";
    return this.evmBalanceUSD.toLocaleString(2);
  }

  get methodBalanceUSDText(): string {
    if (this.isUpdateBalance) return "--";
    return this.methodBalanceUSD.toLocaleString(2);
  }

  get evmbalanceText(): string {
    if (this.ava_asset !== null) {
      let denom = this.ava_asset.denomination;
      return this.evmBalanceBig.toLocaleString(denom);
    } else {
      return "?";
    }
  }

  get methodbalanceText(): string {
    if (this.ava_asset !== null) {
      let denom = this.ava_asset.denomination;
      return this.methodBalanceBig.toLocaleString(denom);
    } else {
      return "?";
    }
  }

  get platformBalance() {
    return this.$store.getters["Assets/walletPlatformBalance"];
  }

  get platformUnlocked(): BN {
    return this.platformBalance.available;
  }

  get platformMultisig(): BN {
    return this.platformBalance.multisig;
  }

  get platformLocked(): BN {
    return this.platformBalance.locked;
  }

  get platformLockedStakeable(): BN {
    return this.platformBalance.lockedStakeable;
  }

  get pBalanceText() {
    if (!this.ava_asset) return "--";
    if (this.isUpdateBalance) return "--";

    let denom = this.ava_asset.denomination;
    let bal = this.platformUnlocked;
    let bigBal = Big(bal.toString());
    bigBal = bigBal.div(Math.pow(10, denom));

    if (bigBal.lt(Big("1"))) {
      return bigBal.toLocaleString(9);
    } else {
      return bigBal.toLocaleString(3);
    }
  }

  get stakingAmount(): BN {
    return this.$store.getters["Assets/walletStakingBalance"];
  }

  get stakingText() {
    let balance = this.stakingAmount;
    if (!balance) return "0";
    if (this.isUpdateBalance) return "--";

    let denom = 9;
    let bigBal = Big(balance.toString());
    bigBal = bigBal.div(Math.pow(10, denom));

    if (bigBal.lt(Big("1"))) {
      return bigBal.toString();
    } else {
      return bigBal.toLocaleString();
    }
  }

  get isUpdateBalance(): boolean {
    if (!this.wallet) return true;
    return this.wallet.isFetchUtxos;
  }

  get priceDict(): priceDict {
    return this.$store.state.prices;
  }

  get hasLocked(): boolean {
    return (
      !this.avmLocked.isZero() ||
      !this.platformLocked.isZero() ||
      !this.platformLockedStakeable.isZero()
    );
  }
  get hasMultisig(): boolean {
    return !this.avmMultisig.isZero() || !this.platformMultisig.isZero();
  }

  get activeWallet(): WalletType | null {
    return this.$store.state.activeWallet;
  }

  get address() {
    let wallet = this.activeWallet;
    if (!wallet) {
      return "-";
    }
    return wallet.getCurrentAddressPlatform();
  }

  get avmUTXOs(): AVMUTXO[] {
    if (!this.wallet) return [];
    let utxos = this.wallet.getUTXOSet().getAllUTXOs();
    let sorted = utxos.sort(this.sortFnc);
    return sorted;
  }

  get platformUTXOs(): PlatformUTXO[] {
    if (!this.wallet) return [];
    let utxos = this.wallet.getPlatformUTXOSet().getAllUTXOs();
    let sorted = utxos.sort(this.sortFnc);
    return sorted;
  }

  get isEmpty() {
    if (this.chain === "V") {
      return this.avmUTXOs.length === 0;
    } else {
      return this.platformUTXOs.length === 0;
    }
  }

  sortFnc<UTXO extends AVMUTXO | PlatformUTXO>(a: UTXO, b: UTXO) {
    let aOut = a.getOutput();
    let bOut = b.getOutput();

    let aType = aOut.getTypeID();
    let bType = bOut.getTypeID();

    if (aType === bType) {
      let aLock = aOut.getLocktime().toNumber();
      let bLock = bOut.getLocktime().toNumber();

      if (aType === PlatformVMConstants.STAKEABLELOCKOUTID) {
        let aStakeLock = (aOut as StakeableLockOut)
          .getStakeableLocktime()
          .toNumber();
        let bStakeLock = (bOut as StakeableLockOut)
          .getStakeableLocktime()
          .toNumber();

        aLock = Math.max(aLock, aStakeLock);
        bLock = Math.max(bLock, bStakeLock);
      }

      if (aLock !== bLock) return bLock - aLock;
      return 0;
    } else {
      if (aType === AVMConstants.SECPXFEROUTPUTID) {
        return -1;
      } else if (bType === AVMConstants.SECPXFEROUTPUTID) {
        return 1;
      }

      if (aType === AVMConstants.NFTXFEROUTPUTID) {
        return -1;
      } else if (bType === AVMConstants.NFTXFEROUTPUTID) {
        return 1;
      }

      if (aType === AVMConstants.NFTMINTOUTPUTID) {
        return -1;
      } else if (bType === AVMConstants.NFTMINTOUTPUTID) {
        return 1;
      }

      if (aType === AVMConstants.SECPMINTOUTPUTID) {
        return -1;
      } else if (bType === AVMConstants.SECPMINTOUTPUTID) {
        return 1;
      }

      // if(aType === AVMConstants.)
    }

    return 0;
  }
  mounted() {
    this.updateQR();
  }
}
